article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

html {
    box-sizing: border-box;
}
  
*,
*:before,
*:after {
    box-sizing: inherit;
}

strong {
	font-weight: 600;
}

:root {
	--primary-bg-color: #94a3b8;
	--secondary-color: #059669;
	--secondary-color-hover: #10b981;
	--secondary-color-active: #34d399;
	--secondary-bg-color: #475569;
	--todo-color: #e4e4e7;
	--error-hover: #f87171;
	--error-active: #fca5a5;
	--font: 'Montserrat', Arial, Helvetica, sans-serif;
}

/* Base layout */
body {
    display: grid;
    grid-template-columns: min-content 1fr;
	height: 100vh;
	font-family: "Montserrat", Arial, Helvetica, sans-serif;
}

i {
	padding: 2px;
}

nav {
    background-color: var(--secondary-bg-color);
}

main {
    background-color: var(--primary-bg-color);
	overflow-y: auto;
	box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
}

/* Sidebar */
.sidebar {
	padding: 20px;
	width: 330px;
	display: flex;
	flex-direction: column;
	gap: 40px;
	color: white;
}

.logo > h1 {
	font-size: 2rem;
	font-weight: 900;
	color: var(--secondary-color-active);
}

.list-container {
	font-size: 1.3rem;
}

.categories-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.categories-header h3 {
	font-weight: 700;
}

.list-header, .categories-header {
	margin-bottom: 30px;
}

#category-list {
	display: flex;
	flex-flow: column nowrap;
	gap: 15px;
}

#category-list li {
	display: flex;
	flex-flow: row-reverse nowrap;
	justify-content: space-between;
	gap: 10px;
	font-size: 1.2rem;
	align-items: center;
	font-weight: 500;

}

.category-list-item {
	display: flex;
	flex-flow: row-reverse nowrap;
	overflow-wrap: anywhere;
	gap: 10px;
	cursor: pointer;
	font-size: 1.1rem;
	align-items: center;
}

.category-list-item:hover {
	color: var(--secondary-color-hover);
}

.category-list-btns {
	display: flex;
	gap: 4px;
}

footer {
	display: flex;
	flex-grow: 1;
	align-items: flex-end;
	justify-content: space-between;
}

footer p {
	font-size: 1.1rem;
	font-weight: 500;
}

footer a {
	text-decoration: none;
	color: var(--secondary-color-hover);
}

footer a:hover {
	color: var(--secondary-color-active)
}

.source {
	color: white;
}

.btn {
	appearance: none;
	align-self: flex-start;
	padding: 6px;
	border-radius: 100%;
	border: transparent;
	cursor: pointer;
	font-size: 18px;
	font-family: var(--font)
}

.btn:hover {
	background-color: var(--secondary-color-hover);
}

.btn:active {
	background-color: var(--secondary-color-active);
}

.btn-delete-todo:hover {
	background-color: var(--error-hover);
}

.btn-delete-todo:active {
	background-color: var(--error-active);
}

/* Modals */
.modal {
	padding: 20px;
	border: 0;
	border-radius: 20px;
	box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
	max-width: 300px;
}

.modal h3 {
	margin-bottom: 4px;
	font-size: 1.4rem;
	font-weight: 600;
}

.modal form {
	display: flex;
	flex-direction: column;
	gap: 12px;
	font-size: 1.1rem;
}

.form-element > button {
	appearance: none;
	padding: 6px;
	border: 1px solid black;
	background-color: var(--secondary-color);
	border-radius: 14px;
	cursor: pointer;
	font-family: 'Montserrat', sans-serif;
	font-weight: 600;
}

.form-element > button:hover {
	background-color: var(--secondary-color-hover);
}

.form-element > button:active {
	background-color: var(--secondary-color-active);
}

.modal .form-element > textarea {
	padding: 4px;
	font-family: var(--font);
	font-size: 1.1rem;
}

.modal .form-element > input {
	padding: 4px;
	font-size: 1.1rem;
}

.modal .form-element > select {
	padding: 4px;
	font-size: 1.1rem;
}

.modal .form-element label > input[type="checkbox"] {
	margin: 0;
	margin-right: 10px;
}

.form-element {
	font-weight: 500;
	display: flex;
	flex-direction: column;
	gap: 4px;
}

.form-element small {
	font-size: 0.9rem;
	font-weight: 200;
}

.input-error-label {
	color: red;
}

.input-error {
	border: 2px solid red;
}

#delete-category-modal h3{
	margin-bottom: 20px;
	font-size: 1.2rem;
	text-align: center;
}

::backdrop {
	background-color: rgba(0, 0, 0, 0.35);
}

/* Todos display */
.main-container {
	margin: 20px;
	padding: 40px;
	background-color: white;
	border-radius: 20px;
	min-height: 97vh;
}

.category-info {
	font-weight: 600;
	font-size: 2rem;
}

.category-symbol {
	margin-right: 14px;
}

.todos-container {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
	gap: 20px;
}

.todos-container h2 {
	text-align: center;
	font-weight: 600;
	font-size: 1.2rem;
}

.todos-header {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 40px;
}

.todos-header-add-new {
	appearance: none;
	padding: 14px;
	background-color: var(--secondary-color);
	color: white;
	border: transparent;
}

.todos-header-edit-category {
	appearance: none;
	padding: 14px;
	background-color: var(--secondary-bg-color);
	color: white;
	border: transparent;
}

.todos-header-edit-category:hover {
	background-color: var(--primary-bg-color);
}

.todos-header-edit-category:active {
	background-color: #b8c7dd;
}

.todo {
	padding: 20px;
	display: flex;
	flex-flow: column wrap;
	overflow-wrap: anywhere;
	gap: 14px;
	background-color: var(--todo-color);
	border-radius: 20px;
	width: 100%;
}

.todo-priority-low {
	background-color: #75b798;
}

.todo-priority-medium {
	background-color: #ffda6a;
}

.todo-priority-high {
	background-color: #ea868f;
}

.todo-checked {
	background-color: #7c8ca3;
}

.todo-checked h4, .todo-checked p {
	text-decoration: line-through;
}


.todo h4 {
	font-weight: 600;
}

.todo-button-container {
	display: flex;
	flex-direction: row-reverse;
	gap: 10px;
	flex-grow: 1;
}

.todo-button-container button {
	align-self: end;
}

.hidden {
	visibility: hidden;
}

.mobile-container {
	display: none;
}

/* Avoid event delegation with this piece of code */
button > * {
	pointer-events: none;
}

/* Responsive */
@media only screen and (max-width: 850px) {
	body {
		display: flex;
		height: 100vh;
		font-family: "Montserrat", Arial, Helvetica, sans-serif;
	}

	main {
		width: 100%;
	}

	.sidebar {
		display: none;
	}

	.main-container {
		margin: 10px;
		padding: 20px;
		padding-top: 30px;
	}

	.todos-header {
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 20px;
	}

	.todos-header button {
		margin: 0 auto;
		border-radius: 20px;
	}

	.todos-header-edit-category::after {
		content: "Edit";
		margin-left: 10px;
		font-weight: 500;
	}

	.todos-header-add-new::after {
		content: "Add";
		margin-left: 10px;
		font-weight: 500;
	}

	.mobile-sidebar {
		display: flex;
		position: fixed;
		width: 100vw;
		height: 100vh;
	}

	.categories-header {
		flex-direction: row;
		gap: 20px;
		align-items: center;
	}

	.category-info {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 10px;
	}

	.category-info span {
		font-size: 1.5rem;
	}

	.category-symbol {
		margin: 0;
	}

	.todos-container {
		grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
	}

	.mobile-container {
		display: block;
		position: fixed;
	}

	.mobile-container-left {
		top: 20px;
		left: 20px;
	}

	.mobile-container-right {
		top: 20px;
		right: 20px;
	}

	.mobile-container-left button::after {
		content: "Menu";
	}

	.mobile-container-right button::after {
		content: "Close";
	}

	.mobile-btn {
		appearance: none;
		border-radius: 20px;
		padding: 12px;
		background-color: var(--secondary-color);
		border: 1px solid transparent;
		font-family: var(--font);
		font-weight: 600;
		font-size: 1rem;
	}

	.mobile-btn:hover {
		background-color: var(--secondary-color-hover);
	}

	.mobile-btn:active {
		background-color: var(--secondary-color-active);
	}

